.navigation {
  width: 100%;
  height: 8rem;
  z-index: 300;
  position: fixed;
  //background-color: $color-primary-light;

  @include respond(phone) {
    height: 7rem;
  }

  &__bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 6rem;

    @include respond(phone) {
      justify-content: center;
    }
  }

  &__icon::before {
    color: rgba($color-grey-dark-3, 0.8);
    font-weight: 700;
    transition: all 0.3s;
    font-size: 4.5rem;
    padding-bottom: 1rem;
    content: '\f083';

    @include respond(phone) {
      font-size: 3.8rem;
      padding: 0.2rem 0;
    }
  }

  &__list {
    display: flex;
    list-style: none;
    font-size: $default-font-size;
    padding: 0 2rem 1rem 0;

    // background-color: burlywood;

    @include respond(phone) {
      margin: 0rem;
      padding: 0.2rem 0;
    }
  }

  &__item {
    display: flex;
    margin-left: 5rem;

    @include respond(tab-port) {
      margin-left: 3rem;
    }

    @include respond(phone) {
      margin-left: 2rem;
    }
  }

  &__link {
    color: $color-white;
    text-decoration: none;
    text-transform: uppercase;
    background-color: rgba($color-grey-dark-3, 0.9);
    padding: 1rem 1.9rem;
    border-radius: 2rem;
    letter-spacing: 1px;
    font-weight: 400;
    transition: all 0.2s;

    &:hover {
      transform: translateY(-3px);
      box-shadow: 0 1rem 2rem rgba($color-primary-light, 0.6);
      background-color: rgba($color-primary-dark, 0.8);

      &::after {
        transform: scaleX(1.4) scaleY(1.6);
        opacity: 0;
      }
    }

    @include respond(phone) {
      padding: 0.8rem 1.1rem;
    }
  }

  .navigation__bar.sticky {
    z-index: 300;
    position: fixed;
    background-color: rgba($color-white, 0.9);
  }
}
